  methods: {
    // Ê£ÄÊü•Á≥ªÁªüÁä∂ÊÄÅ
    async checkSystemStatus() {
      try {
        // Ê£ÄÊü•Êï∞ÊçÆÂ∫ìËøûÊé•
        const result = await commonManagement.getAdminStats()
        if (result.success) {
          this.dbStatus = 'connected'
        } else {
          this.dbStatus = 'error'
        }
        
        // Ê£ÄÊü•‰∫ëÂáΩÊï∞Áä∂ÊÄÅ
        this.cloudStatus = 'ready'
        
        // Ëé∑ÂèñÂ≠òÂÇ®‰ΩøÁî®ÊÉÖÂÜµ
