        // ËÆ°ÁÆóËøêË°åÊó∂Èó¥
        this.uptime = '1Â§©2Â∞èÊó∂'
      } catch (error) {
        console.error('Ê£ÄÊü•Á≥ªÁªüÁä∂ÊÄÅÂ§±Ë¥•Ôºö', error)
        this.dbStatus = 'error'
        this.cloudStatus = 'error'
      }
    },
    
    // Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØ
    getSystemInfo() {
      const systemInfo = uni.getSystemInfoSync()
