        title: 'ÂäüËÉΩÂºÄÂèë‰∏≠',
        icon: 'none'
      })
    },

    // ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ì
    async initDatabase() {
      if (this.loading) return
      
      this.loading = true
      uni.showLoading({
        title: 'ÂàùÂßãÂåñ‰∏≠...'
      })

      try {
        // ‰ΩøÁî®‰∫ëÂØπË±°ÂàùÂßãÂåñÊï∞ÊçÆÂ∫ì
        const commonManagement = uniCloud.importObject('common-management')
        const result = await commonManagement.initDatabase()
        
        if (result.success) {
          uni.hideLoading()
          uni.showToast({
            title: 'ÂàùÂßãÂåñÊàêÂäü',
            icon: 'success'
          })
          
          console.log('Êï∞ÊçÆÂ∫ìÂàùÂßãÂåñÂÆåÊàê')
          this.checkSystemStatus() // ÈáçÊñ∞Ê£ÄÊü•Áä∂ÊÄÅ
        } else {
          throw new Error(result.message)
        }
      } catch (error) {
        uni.hideLoading()
        uni.showToast({
          title: 'ÂàùÂßãÂåñÂ§±Ë¥•',
          icon: 'none'
        })
        console.error('Êï∞ÊçÆÂ∫ìÂàùÂßãÂåñÂ§±Ë¥•:', error)
      } finally {
        this.loading = false
      }
    },

    // Ê∏ÖÁ©∫ÊµãËØïÊï∞ÊçÆ
    clearTestData() {
